import request from './request'
//导出报告文件
export function exportReportFile(data){
    return request({
        method:'POST',
        url:'export',
        data:{
            selectData:data
        }
    })
} 
//获取log数据
export function getAllLogItems(){
    return request({
        method:'GET',
        url:'main',
    })
}
//写入log数据
export function writeLogItems(data){
    return request({
        method:"POST",
        url:"main",
        data:{
            logItems:data
        }
    })
}